/*-------------------------------------------------services-----------------------------------------------*/



@media only screen and (max-width: 1920px) {

    .note {
        top: 8%;
        left: 20%;
        position: absolute;

        z-index: 99;

        mix-blend-mode: difference ;
        color: #D1D1D1;
        text-align: center;
        font-family: Helvetica;
        font-style: normal;
        text-transform: uppercase;
        font-size: 4rem;
        mix-blend-mode: difference;
      
    }
    .note2 {
        top: 8%;
        left: 20%;
        position: absolute;

        z-index: 99;

        mix-blend-mode: difference ;
        color: #D1D1D1;
        text-align: center;
        font-family: Helvetica;
        font-style: normal;
        text-transform: uppercase;
        font-size: 4rem;
        mix-blend-mode: difference;
    
    }
    .ServH {
        font-size: 1.5rem;
        mix-blend-mode: difference ;
        font-weight: 50;
    }
    .ServP {
        font-size: .8rem;
        mix-blend-mode: difference ;
        
    }
    .outer {
        
        z-index: 1;
        width: 100%;
        height: 100%;
        overflow:scroll;

        
    }
    .outer::-webkit-scrollbar{
        display: none;
    }

    .outer .inner {
     
        
        left: 0;
        top: 25%;
        position: relative;
        width: 200%;
        height: 50%;
    
    }
    
    .outer .inner div.box {
        position: relative; 
        display: grid;
        place-items: center;
        overflow: hidden;

        transition: 1.2s;
        &:hover {
            background-color: white;
        }
        margin: 0 1px 0 0;
        background-color: black;
        border-radius: 16px;
        width: 15.94%;
        height: 100%;
        float: left;
    }
    


    .border_1_1 {
        transition:1.2s;
        &:hover {
            background-color: white;
        }
        margin: 0 1px 1px 0;
        background-color: black;
        border-radius: 16px;
        width: 10%;
        height: 100%;
        float: left;
    }
    .border_1_2 {
        transition: 1.2s;
        &:hover {
            background-color: white;
        }
        margin: 0 1px 1px 0;
        background-color: black;
        border-radius: 16px;
        width: 10%;
        height: 100%;
        float: left;
    }

    #services_1 {
        width: 9.9%; 
        height: 24.9%; 
        left: 90%; 
        top: 0; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
      
    }

    #services_2 {
        width: 9.9%; 
        height: 24.9%; 
        left: 0; 
        top: 75.1%; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
  
      
    }

    #services_3 {
        width: 89.9%; 
        height: 24.9%; 
        left: 0; 
        top: 0; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
       
    }

    #services_4 {
        width: 89.9%; 
        height: 24.9%; 
        left: 10%; 
        top: 75.1%; 
        position: absolute; 
        transform: 0; 
        transform-origin: 0 0;
        border-radius: 16px;
      
    }


}

